body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue", Helvetica, "Hiragino Sans GB", Arial, sans-serif;
  font-size: 16px;
}

.BackgroundImage {
  position: fixed;
  z-index: -100;
  object-fit: cover;
}

.Background {
  position: fixed;
  right: 0;
  top: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
}
